import { PngIcoConverter } from './png2icojs';
import JSZip from 'jszip';
import { fileTypeFromBlob } from 'file-type';
//------------------------------------------------------------------------------
async function getThumbnail(thumbnailUrl) {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage(
            {
                action: "fetchThumbnail",
                url: thumbnailUrl
            },
            (response) => {
                if (response && response.data) {
                    // ArrayBuffer  Blob 
                    const byteArray = new Uint8Array(response.data);
                    const blob = new Blob([byteArray]);
                    resolve(blob);
                } else {
                    reject(new Error(""));
                }
            }
        );
    });
}

export async function getIconFromPngUrl(thumbnailUrl) {
    try {
        // Blob
        const imageData = await getThumbnail(thumbnailUrl);

        // PNGCanvas
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        const size = 256; // ICO
        canvas.width = size;
        canvas.height = size;

        // 
        await new Promise((resolve, reject) => {
            const img = new Image();
            img.onload = () => {
                ctx.drawImage(img, 0, 0, size, size);
                // 
                URL.revokeObjectURL(img.src);
                resolve();
            };
            img.onerror = (e) => reject(new Error(`: ${e}`));
            img.src = URL.createObjectURL(imageData);
        });

        // CanvasPNG Blob
        const pngBlob = await new Promise((resolve, reject) => {
            canvas.toBlob((blob) => {
                if (!blob) reject(new Error("PNG blob"));
                else resolve(blob);
            }, 'image/png');
        });

        // PNG BlobICO
        const converter = new PngIcoConverter();
        const icoBlob = await converter.convertToBlobAsync([{ png: pngBlob }]);

        return icoBlob;
    } catch (error) {
        console.error("ICO:", error);
        throw error;
    }
}
//------------------------------------------------------------------------------
// Uint8Array 1 Uint8Array 
function concatenateUint8Arrays(arrays) {
    // 
    const totalLength = arrays.reduce((acc, array) => acc + array.length, 0);
    // 
    const result = new Uint8Array(totalLength);
    // 
    let offset = 0;
    for (const array of arrays) {
        result.set(array, offset);
        offset += array.length;
    }
    return result;
}

async function getItemBlob(downloadUrl) {
    return new Promise((resolve, reject) => {
        // 
        const receivedChunks = [];
        let totalChunks = 0;
        let blobType = 'application/zip';

        // 
        const chunkListener = (message) => {
            if (message.action === "receiveChunk") {
                console.log(`: ${message.chunkIndex + 1}/${message.totalChunks}`);

                // Base64URL
                const prefix = message.dataUrl.split(',')[0];
                console.log("prefix : ", prefix);
                const base64Data = message.dataUrl.split(',')[1];

                // Base64
                const byteCharacters = atob(base64Data);
                const byteNumbers = new Array(byteCharacters.length);
                for (let i = 0; i < byteCharacters.length; i++) {
                    byteNumbers[i] = byteCharacters.charCodeAt(i);
                }
                const byteArray = new Uint8Array(byteNumbers);

                // 
                receivedChunks[message.chunkIndex] = byteArray;

                // Blob
                if (receivedChunks.filter(Boolean).length === totalChunks) {
                    // 
                    const completeData = concatenateUint8Arrays(receivedChunks);
                    console.log("saving as type : ", blobType);
                    const blob = new Blob([completeData], { type: blobType });
                    // 
                    chrome.runtime.onMessage.removeListener(chunkListener);
                    resolve(blob);
                }
            }
        };

        // 
        chrome.runtime.onMessage.addListener(chunkListener);

        // 
        chrome.runtime.sendMessage(
            {
                action: "fetchItem",
                url: downloadUrl
            },
            (response) => {
                if (chrome.runtime.lastError) {
                    chrome.runtime.onMessage.removeListener(chunkListener);
                    reject(new Error(": " + chrome.runtime.lastError.message));
                    return;
                }

                if (response.error) {
                    chrome.runtime.onMessage.removeListener(chunkListener);
                    reject(new Error(response.error));
                    return;
                }

                if (response.status === "start") {
                    console.log(`: ${response.totalChunks}, ${response.totalSize}`);
                    totalChunks = response.totalChunks;
                    blobType = response.type || blobType;
                    console.log("filetype : ", blobType);
                }
            }
        );
    });
}
//------------------------------------------------------------------------------
async function createZipArchive(fileMap) {
    const zip = new JSZip();
    // 
    for (const [fileName, blob] of fileMap) {
        zip.file(fileName, blob);
    }
    return zip.generateAsync({ type: "blob" });
}

export async function downloadWithZip(itemDownloadUrl, thumbnailUrl, itemFileName) {
    console.log("downloading...");
    const mimeToExt = {
        'application/zip': 'zip',
        'application/x-zip-compressed': 'zip',
        'application/pdf': 'pdf',
        'application/json': 'json',
        'application/octet-stream': 'bin',
        'image/png': 'png',
        'image/jpeg': 'jpg',
        'image/gif': 'gif',
        'image/webp': 'webp',
        'image/vnd.adobe.photoshop': 'psd',
        'text/plain': 'txt',
        'text/html': 'html',
        'text/css': 'css',
        'text/javascript': 'js',
        'application/javascript': 'js',
        'audio/mpeg': 'mp3',
        'audio/wav': 'wav',
        'video/mp4': 'mp4',
        'application/vnd.rar': 'rar',
        'application/x-rar-compressed': 'rar'
        // 
    };
    try {
        // Blob
        const productFileBlob = await getItemBlob(itemDownloadUrl);
        // const ext = mimeToExt[productFileBlob.type];
        const filetype = await fileTypeFromBlob(productFileBlob);//=> {ext: 'txt', mime: 'text/plain'}
        console.log("filetype from fileTypeFromBlob : ", filetype);
        const ext = filetype['ext'];
        // BlobICO
        const icoBlob = await getIconFromPngUrl(thumbnailUrl);
        //zip
        const fileMap = new Map();
        fileMap.set(`boothThumbnail.ico`, icoBlob);//icon
        fileMap.set(itemFileName + `.${ext}`, productFileBlob);// 
        fileMap.set("desktop.ini", `[.ShellClassInfo]\nIconResource=boothThumbnail.ico,0\n[ViewState]\nMode=\nVid=\nFolderType=Generic`);//desktop.ini
        // Zip
        const zipBlob = await createZipArchive(fileMap);

        // 
        chrome.runtime.sendMessage({
            action: 'downloadZip',
            blobUrl: URL.createObjectURL(zipBlob),
            filename: itemFileName + '.zip'
        });
    } catch (error) {
        console.error('Zip:', error);
    }
}